@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
.featured{border-left:6px solid #5ba4a4}@-webkit-keyframes pulse{from{transform:scale3d(1, 1, 1)}50%{transform:scale3d(1.005, 1.005, 1.005)}to{transform:scale3d(1, 1, 1)}}@keyframes pulse{from{transform:scale3d(1, 1, 1)}50%{transform:scale3d(1.005, 1.005, 1.005)}to{transform:scale3d(1, 1, 1)}}.job{display:flex;padding:20px 32px;background-color:#fff;margin:16px 0;align-items:center;box-shadow:0 0.25rem 0.125rem 0 rgba(0,0,0,0.05);border-radius:8px;transition:all 200ms ease-in-out;transform:scale(1);transform-origin:50% 50%}.job:hover{cursor:pointer;transform:scale(1.005) perspective(1px);transform-origin:51% 51%;box-shadow:0 0.8rem 0.8rem 0 rgba(0,0,0,0.05)}.job .icon{margin:0 24px 0 0}.job .info{flex:2 1;display:flex;flex-direction:column}.job .info .title-section{display:flex;align-items:center;margin-bottom:16px}.job .info .title-section .title{color:#5ba4a4;font-size:0.8rem;font-weight:bold}.job .info .title-section .badges{margin-left:12px}.job .info .title-section .badges span{margin:0 4px;padding:6px;font-size:0.5rem;text-transform:uppercase;background-color:#5ba4a4;border-radius:16px;color:#fff}.job .info .title-section .badges span:last-child{background-color:#2c3a3a}.job .info h1{font-size:1.2rem;color:#2c3a3a}.job .info .sub-info{color:#7b8e8e;margin-top:8px}.job .info .sub-info span{margin:0 12px;font-size:0.8rem;position:relative}.job .info .sub-info span:first-child{margin-left:0}.job .info .sub-info span:last-child::after{content:''}.job .info .sub-info span::after{content:' • ';position:absolute;right:-18px;color:#7b8e8e;font-size:1.5rem;top:8px;line-height:0}.job .tags{justify-content:right;flex:1 1;display:flex}.job .tags ul{padding:0;display:flex}.job .tags ul li{list-style:none;margin:0 6px;padding:12px;background-color:#eef6f6;color:#5ba4a4;font-weight:bold;font-size:70%;border-radius:6px;transition:all 200ms ease-in-out}.job .tags ul li:hover{background-color:#5ba4a4;color:#fff}

.App .header{height:156px;width:100%;display:block;background-image:url(/job-listings-frontendmentor/static/media/bg-header-desktop.6e2f7196.svg);background-origin:content-box;background-color:#5ba4a4}.App .list-jobs{max-width:900px;margin:80px auto 80px;display:flex;flex-direction:column}.App .list-jobs .job:first-child{margin-top:0}.App .list-jobs .job:last-child{margin-bottom:0}

*{margin:0;padding:0;box-sizing:border-box}html,body,#root{height:100vh}body{background:#effafa;font-size:15px}body,input,button,textarea{font-family:'Spartan', sans-serif}

